.portfolio__features-container__feature {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-direction: row;

    margin: 1rem;
}

.portfolio__features-container__feature-title{
    flex: 1;
    max-width: 180px;
    margin-right: 2rem;
}

.portfolio__features-container__feature-title h1{
    font-size: 18px;
    line-height: 24px;
    font-weight: 800;
    font-family: var(--font-family);
    letter-spacing: -0.04em;
    color: #ffffff;
}

.portfolio__features-container__feature-title div{
    width: 38px;
    height: 3px;
    background: var(--gradient-bar);
    box-shadow: 0 4px 4px rgba(0,0,0,0);
    margin-bottom: 0.25rem;
}

.portfolio__features-container__feature-text{
    flex: 2;
    max-width: 390px;
    display: flex;
}

.portfolio__features-container__feature-text p{
    font-size: 14px;
    line-height: 24px;
    font-weight: 500;
    font-family: var(--font-family);
    letter-spacing: -0.04em;
    color: var(--color-text);
}

@media screen and (max-width: 550px) {

    .portfolio__features-container__feature-title h1{
        font-size: 14px;
        line-height: 22px;
    }

    .portfolio__features-container__feature-title p{
        font-size: 12px;
        line-height: 20px;
    }

    .portfolio__features-container__feature {
        margin: 1rem 0;
    }
}